.section-choose{
    height: 25rem;
    padding: 7.2rem 15%;
    display: grid;
    gap: 7.2rem;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: repeat(2,1fr);
    margin-bottom: 10rem;   
    justify-content: center;
}
.choose-header{
    grid-column: 1/-1;
    justify-self: center;
    font-family: "Rubic",sans-serif;
    font-size: 2.2rem;
    font-weight: 600;
    color: #333;
}
.choose-grid-item{
    align-self: center;
}
.flexy{
    display: flex;
    align-items: center;
    gap:1.2rem;
}
.flexy .info-item-icon:hover{
    color: #333;
    background: none;
}

.flexy .info-item-icon{
    height: 7.8rem;
    width: 7.8rem;
}