.hero{
    background-image: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2)),url('../assets/img/hero_img.png');
    width: 100%;
    height: 100vh;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.hero-title{
    display: flex;
    position: absolute;
    top:30%;
    left: 27%;
    transform: translate(-20%,-20%);
    gap: 8.5rem;
}
.hero-text{
    font-family: "Rubik",sans-serif;
    font-size: 5.4rem;
    color: whitesmoke;
    width: 60%;
    line-height: 1.1;
    margin-bottom: 8.4rem;
    font-weight: 500;
}
.slider-bar{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;  
}
.slider-bar p{
    font-family: "Rubik",sans-serif;
    color: white;
    font-size: 1.6rem;
    font-weight: 500;
}
.slider-line{
    height: 30rem;
    width: 0.2rem;
    background-color: rgba(245, 245, 245,0.3);
}
.scroll-line{
    height: 7rem;
    width: 0.2rem;
    background-color: white;
}
.shop-now{
    height: 3.5rem;
    width: 3.5rem;
    background-color: #FBB03B;
    border-radius: 50%;
    padding: 0.6rem 0.6rem;
    display: flex;
    gap: 1.4rem;
    align-self: center ;
} 
.shop-now-btn{
    height: 2.2rem;
    width: 2.2rem;
    color: whitesmoke;
}
.shop-now::after{
    display: inline-block;
    font-family: "Rubik",sans-serif;
    content:'shop now';
    text-transform: uppercase;
    font-size: 1.4rem;
    color: whitesmoke;
    font-weight: 600;
    letter-spacing: 0.15rem;
    margin-left: 0.8rem;
}

.hero-info{
    background-color: white;
    height: 14rem ;
    width: 70%;
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: 0px 75px 0px 0px;
}
.hero-info-list{
    height: 100%;
    display: flex;
    justify-content: flex-end;  
    gap:4.8rem;
  
}
.slider-btns{
    display: flex;
    gap: 2.1rem;
    position: absolute;
    right: 15%;
    bottom: 7%;
}
.slider-btn-left,
.slider-icon{
    height: 7.4rem;
    width: 7.4rem;
    background-color: rgba(153, 153, 153,0.3);
    color: whitesmoke;
}
.slider-btn-link:hover,
.slider-icon:hover{
    color: #333;
    background-color: whitesmoke;
}