.header{
    display: flex;
    justify-content: space-between;
    height: 9.6rem;
    width: 100%;
    align-items: center;
    padding: 0 17%;
    position: absolute;
    z-index: 1;
}
.logo-box{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:1.8rem;
    font-weight: 600;
}
.logo-icon-link:link,
.logo-icon-link:visited,
.logo-text-link:link,
.logo-text-link:visited{
    text-decoration: none;
    font-size: 1.8rem;
    color: whitesmoke;
}
.logo-cion{
    height: 5.4rem;
    width: 5.4rem;
    color: whitesmoke; 
    transition: all 0.3s;   
}
.logo-cion:hover{
    transform: rotate(-10deg);
    color: #fd7e14;
}
.logo-text-link{
    transition: all 0.3s;
}
.logo-text-link:hover{
    color: #fd7e14;

}
.btn-mobile-nav{
    display: none;
    height: 3.4rem;
    width: 3.4rem;
    background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
}
.btn-mobile-nav-icon{
    height: 100%;
    width: 100%;
    color: whitesmoke;
    cursor: pointer;
}
.main-nav-list{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 4.8rem;

}
.main-nav-link{
    padding: 1.2rem 0.8rem;
    transition: all 0.3s;
}
.main-nav-link:link,
.main-nav-link:visited{
    color: whitesmoke;
    font-size: 1.6rem;
    text-decoration: none;
    font-weight: 600;
    letter-spacing: 0.3rem;
    display: inline-block;
    font-family: "Rubik",sans-serif;
}
.main-nav-link:hover,
.main-nav-link:active{ 
    box-shadow:  0 4px 2px -2px #fd7e14
}
.nav-icon-list{
    display: flex;
    list-style: none;
    justify-content: center;
    gap:4.8rem;
}
.nav-icon{
    height: 3.8rem;
    width: 3.8rem;
    color: whitesmoke;
    padding: 0.4rem 0.4rem;
    border-radius: 50%;
    transition: all 0.3s;
}
.nav-icon:hover,
.nav-icon:visited{
    transform: rotate(10deg);
    color: #fd7e14;
}