.banner-box{
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 2.8rem;
    height: 45rem;
    padding: 2% 8%; 
}
.banner-img1{
    background-image: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2)),url('../assets/img/banner1.png');
}
.banner-img2{
    background-image: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2)),url('../assets/img/banner2.png');
}
.banner-img3{
    background-image: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2)),url('../assets/img/banner3.png');
}
.banner-img1,
.banner-img2,
.banner-img3{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
}
.banner-sub-box{
    position: relative;
    top:50%;
    left:10%;
}
.banner-title{
    width: 20rem;
    color: whitesmoke;
    font-size: 2.6rem;
    font-family: "Rubik",sans-serif;
    font-weight: 400;
    margin-bottom: 4.2rem;
}
.banner-btn{
    transition: all 0.4s;
}
.banner-img3::after{
    position: absolute;
    content: '-50%';
    padding: 1rem 1rem;
    background-color: red;
    border-radius: 20px;
    font-size: 1.4rem;
    font-family: "Rubik",sans-serif;
    left: 10%;
    top: 35%;
    color: whitesmoke;
    font-weight: 500;
}