.footer{
    height: 20rem;
    margin-top: 10rem;
    padding: 0 7%;
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;
}
.footer-box{
    display: flex;
    flex-direction: column;
    gap:3.2rem;
}
.footer-box-text{
    width:60%;
}
.footer-box-text p{
    font-family: "Rubik",sans-serif;
    font-size: 1.4rem;
    color: #777;
    line-height: 2.4rem;

}
.soc-icon-list{
    display: flex;
    gap:2.4rem;
    list-style: none;
}
.soc-icon{
    height: 2.4rem;
    width: 2.4rem;
    
}
.socicon{
    height: 100%;
    width: 100%;
    color: #777;
}
.footer-logo{
    display: flex;
    align-items: start;
    gap:1.2rem;
}
.footer-logo-cion{
    height: 4.4rem;
    width: 4.4rem;
    color: #333;
}
.footer-text-link{
    font-size: 2.2rem;
    font-family: "Rubik",sans-serif;
    color: #333;
    text-decoration: none;
    margin-top: 1.2rem;
}
.shopping{
    font-family: "Rubik",sans-serif;
    font-size: 1.8rem;
    font-weight: 500;
    margin-bottom: 2.4rem;
}
.shopping-list{
    display: flex;
    flex-direction: column;
    list-style: none;
    gap: 1.4rem;
}
.shopping-item{
    font-family: "Rubik",sans-serif;
    font-size: 1.4rem;
}
.copyright{
    display: flex;
    justify-content: center;
    margin: 2% 7%;
    padding-top: 2%;
    border-top: 0.1rem solid rgba(163, 159, 159, 0.42);
}
.copyright-title{
    font-family: "Rubik",sans-serif;
    font-size: 1.2rem;
    color: #777;
    letter-spacing: 0.2rem;
    font-weight: 400;

}