.section-select{
    padding: 1% 8%;
}
.select-header{
    display: flex;
    justify-content: space-between; 
    align-items: center;
}
.just-for-you{
    font-family: "Rubik",sans-serif;
    font-size: 2.4rem;
    font-weight: 500;
}
.select-btn{
    transition: all 0.4s;
    box-shadow: 0 0 0.3rem gray;;
}
.select-btn:hover{
    border: none;
}
.select-grid {
    padding-top: 4.4rem ;
}
.grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    justify-items: center;
}
.grid-item-img-1,
.grid-item-img-2,
.grid-item-img-3,
.grid-item-img-4{
    background-repeat: none;
    background-size: cover;
    background-position: center;
}
.grid-item-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.2rem;
}
.grid-item-title,
.grid-item-price{
    font-family: "Rubik",sans-serif;
    font-size: 1.6rem;
    font-weight: 500;
    color: #444;
}
.grid-item-price{
    margin-top: 1.8rem;
    color: #222;
}
.grid-item{
    position:relative ;
}
.grid-item:nth-child(2)::after{
    position: absolute;
    content: '-30%';
    padding: 1rem 1rem;
    background-color: red;
    border-radius: 20px;
    font-size: 1.4rem;
    font-family: "Rubik",sans-serif;
    left: 10%;
    top: 5%;
    color: whitesmoke;
    font-weight: 500;
} 
.grid-item-price{
    font-family: "Rubik",sans-serif;
    display: flex;
    gap:1.2rem
}
.grid-item-sale{
    color: rgb(185, 185, 185);
}

