.section-subscribe{
    height: 20rem;
    width: 85%;
    background-image:linear-gradient(to right bottom,rgba(91, 89, 87, 0.35),rgba(49, 47, 45, 0.35)), url('../assets/img/subscribe.png');
    background-position:top;
    background-repeat: none;
    background-size: cover;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5%;
    margin: 2rem auto;
}
.subscribe-title{
    font-size: 2.8rem;
    line-height: 3.8rem;
    font-weight: 500;
    color: white;
    width: 50%;
}
.subscribe-input{
    font-family: inherit;
    height: 4.2rem;
    width: 27rem;
    border-radius:1.8rem;
    border: none;
    margin-left: 0.8rem;
    padding: 1.2rem;
    margin-right: 2.4rem;   
}
