


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;500;700&family=Rubik:wght@400;500;600;700&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
    overflow-x: hidden;    
}

body{
    font-family:  sans-serif;
    line-height: 1;
    font-weight: 400;
    overflow-x: hidden;
    overflow-y: hidden;
}


strong{
    font-weight: 500;
}

.btn:link,
.btn:visited{
    background-color: white;
    padding: 1.2rem 1.2rem;
    text-decoration: none;
    text-transform: uppercase;
    color: #222;
    font-size: 1.2rem;
    font-family: "Rubik",sans-serif;
    font-weight: 500;
    border-radius: 15rem;
}
.btn:hover,
.btn:active{
    background-color:#FBB03B;
    color: white;
}
/* ------------------------------------------------------------- */
.hero-info-item{
    display: flex;
    align-items: center;
    margin-right: 2.5rem;
    width: 25rem; 
}
.info-item-text{
    width: 20rem;
}
.info-item-icon{
    height: 4.8rem;
    width: 4.8rem;
    padding: 0.8rem 1.2rem; 
    background-color: antiquewhite;
    border-radius: 1rem;
    color:#FBB03B; 
    margin-right: 2.5rem;
}

.info-item-title{
    font-size: 1.4rem;
    font-family: "Rubik",sans-serif;
    margin-bottom: 0.8rem;
    color:#333
}
.info-item-text p{
    width: 60%;
    font-size: 1.2rem;
    font-family: "Rubik",sans-serif;
    color: #888;
}
/* --------------------------------------------------------------- */

/* 
max-width: 84 rem = 1344 px
max-width: 75 rem = 1200 px
max-width: 59 rem = 944 px - აქ ვცლით ნავიგაციას ბურგერ აიკონით
max-width: 44 rem = 704 px
max-width: 34 rem = 544 px
 */

 @media (max-width:84rem){
    .header{
        padding: 0 10% !important;
    }

    .slider-line{
        height: 25rem !important;
    }
    .scroll-line{
        height: 5rem !important;
    }
    .hero-text{
        margin-bottom: 3.4rem !important;
    }
    .slider-btns{
        bottom: 5%!important;
        right: 5% !important;
    }
    .hero-info-list{
        gap: 0.2rem !important;
    }
    .banner-box{
        padding: 2% 2% !important;
    }
    .section-select{
        padding: 1% 2% !important;
    }
    .section-choose{
        gap: 4.2rem !important;
        grid-template-columns: repeat(4,25rem) !important;
    }
 }

 @media (max-width:75rem){
    .header{
        height: 7.6rem !important;
    }
    .logo-box{
        gap:1.2rem !important;
        font-weight: 500 !important;
    }
    .logo-cion{
        height: 4.4rem !important;
        width: 4.4rem !important;
    }
    .nav-icon-list{
        gap:2.8rem !important;
    }

    .main-nav-link:link,
    .main-nav-link:visited{
    transition: all 0.4s !important;
    font-size: 1.2rem !important;
    font-weight: 500 !important;
    letter-spacing: 0.2rem !important;
    }

    .nav-icon{
        height: 2.8rem !important;
        width: 2.8rem !important;
        color: whitesmoke;
    }
    .hero-title{
        gap: 5.6rem !important;
    }
    .hero-info-list{
        gap:1rem !important;
      
    }
    .hero-info-item{
        margin-right: 0.1rem !important;
        width: 20rem !important; 
    }
    .info-item-text{
        width: 12rem !important;
    }
    .info-item-icon{
        height: 2.8rem !important;
        width: 2.8rem !important;
        padding: 0.4rem 0.8rem; 
    }
    
    .info-item-title{
        font-size: 1.2rem !important;
    }
    .info-item-text p{
        width: 70% !important;
        font-size: 0.8rem !important;
    }
    .slider-btns{
        gap: 1.6rem !important;
    }
    .slider-btn-left,
    .slider-icon{
    transition: all 0.4s !important;
    height: 5.2rem !important;
    width: 5.2rem !important;
    }
    .banner-box{
        gap: 1.6rem !important;
        
    }
    .banner-img1,
    .banner-img2,
    .banner-img3{
        background-position:right !important;
    }
    .grid {
        grid-template-columns: repeat(4,20rem) !important;
        justify-content: space-around;
        gap:1.2rem !important;
    }
    .grid-item-img-1 img,
    .grid-item-img-2 img,
    .grid-item-img-3 img,
    .grid-item-img-4 img{
   
     width: 100% !important;
    }
    .section-choose{
        height: 25rem;
        padding: 7.2rem 15%;
        display: grid;
        gap: 3.2rem !important;
        grid-template-columns: repeat(4,20rem) !important;
    }
    .flexy{
        gap: 0rem !important;
    }
    .section-subscribe{
        grid-template-columns: repeat(4,20rem) !important;
        width: 95% !important;
    }
    .subscribe-title{
        font-size: 2.2rem !important;
        line-height: 2.6rem !important;
    }
    .footer{
        padding: 0 3% !important;
    }
    .footer-box{
        gap:1.2rem !important;
    }
    .footer-box-text p{
        font-size: 1rem !important;
        line-height: 2rem !important;
    }
    .soc-icon-list{
        gap:1.2rem !important;

    }
    .shopping{
  
        font-size: 1.2rem !important;
        margin-bottom: 2.2rem !important;
    }
    .shopping-list{
        gap: 1rem !important;
    }
    .shopping-item{
        font-size: 1rem !important;
    }
    .copyright-title{
        font-size: 1rem !important;
        letter-spacing: 0.1rem !important;
        font-weight: 500 !important;
    }
 }

 @media (max-width:59rem){
    









    .main-nav{
        display: none !important;
    }
    .nav-icons{
        display: none !important;
    }
    .btn-mobile-nav{
        display: inline-block !important;
    }
    .hero-text{
        font-size: 3.4rem !important;
        width: 60% !important;
    }
    .slider-bar p{
        font-size: 1rem !important;
    }
    .slider-line{
        height: 18rem !important;
    }
    .scroll-line{
        height: 3.5rem !important;
    }
    .shop-now::after{
        font-weight: 500 !important;
    }
    .slider-btns{
        right: 20% !important;
        bottom: 30% !important;
    }
    .banner-box{
        height: 30rem !important;
    }
    .btn:link,
    .btn:visited{
    padding: 0.8rem 0.8rem !important;
    font-size: 0.8rem !important;
    }
    .hero-info{
        width: 100% !important;
        border-radius: 75px 75px 0px 0px !important;
    }
    .select-header{
        margin-top: 3% !important;
    }
    .select-grid{
        grid-template-columns: repeat(2,1fr) !important;
        row-gap: 5.2rem !important;
    }
    .section-choose{
        padding: 7.2rem 5% !important;
        gap: 1.2rem !important;
        grid-template-columns: repeat(2,1fr) !important;
    }
    .flexy{
        justify-content: center !important;
        margin-top: 2.4rem !important;
        
    }
    .subscribe-title{
        font-size: 1.8rem !important;
        line-height: 2.8rem !important;
    }
    .subscribe-input-btn{
        display: flex;
        flex-direction: column;
        gap: 2.4rem !important;
    }
    
    .subscribe-input{
        display: inline-block !important;
        height: 2.6rem !important;
        width: 20rem !important;
        margin-left: 0 !important;
        margin-right: 0 !important;   
    }
  
    .subscribe-btn:link,
    .subscribe-btn:visited{
         padding: 4% 35% !important;
         font-size: 1rem !important;
         font-weight: 400 !important;
    }  
    .footer{
        gap: 2.6rem !important;
    }
    .footer-soc-logo{
        display: none !important;
    }
    .footer-box-text p{
        font-size: 1rem !important;
        line-height: 1.8rem !important;
    }
}
@media (max-width: 34rem){
    .info-item-icon{
        margin-right: 0.5rem !important;
    }
    .hero-info-list{
        gap:0.4rem !important;
        margin-left: 10% !important;
      
    }
    .banner-box{
        grid-template-columns: 1fr !important;
        gap: 3.2rem !important;
        height: 60rem !important;
    }
    .banner-sub-box{
        top:30% !important;
    }
    .banner-btn{
        position: absolute !important;
        right: 20% !important;
        top: 50% !important;
    }
    .banner-img3::after{
        top: 5% !important;
    }
}